ul {
  list-style: none;
  padding: 0;
}

li {
  /* display: flex;
  justify-content: center;
  flex-direction: row; */
  margin-left: auto;
  margin-right: auto;
  border: 1px solid #ccccff;
  border-radius: 30px;
  width: 250px;
  font-size: 20px;
  margin-bottom: 5px;
  color: #ccccff;
}

.navbar {
  -webkit-text-decoration-line: none;
          text-decoration-line: none;
  display: flex;
  justify-content: center;
  flex-direction: row;
}

div {
  text-align: center;
}

.img-center {
  text-align: center;
}

img,
input {
  /* display: block;
  margin-left: auto;
  margin-right: auto; */

  text-align: center;
  border: 1px solid black;
  border-radius: 20px;
  margin: 5px;
}

img {
  height: 200px;
  width: 200px;
  border-radius: 100px;
}

.delete-button {
  border-radius: 10px;
  margin-bottom: 10px;
  background-color: #ccccff;
  margin: 0 10px;
}

button {
  background-color: #ccccff;
}

.filebox label:hover {
  cursor: pointer;
}

.tweet-center {
  text-align: center;
  border: 1px solid #ccccff;
  border-radius: 50px;
  margin-top: 10px;
  padding: 10px;
  background-color: white;
}

h4 {
  font-size: 25px;
}

h6 {
  font-size: 15px;
}

.delete-button,
.edit-button {
  border-radius: 10px;
  margin-left: 10px;
  border: 1px solid;
  background-color: #ccccff;
}

.edit-button {
  margin-right: 10px;
}

.file-input-top {
  display: block;
  margin-right: auto;
  margin-left: auto;
  border: 1px solid black;
  border-radius: 50px;
  background-color: white;
}

.file-input-text {
  display: block;
  margin-left: auto;
  margin-right: auto;
  text-align: center;
  padding: 10px 40px;
}

.file-input-image {
  display: block;
  margin-left: auto;
  margin-right: auto;
}

.send-button {
  display: block;
  margin-left: auto;
  margin-right: auto;
  background-color: #ccccff;
}

.background {
  background-color: black;
}

.auth-form {
  display: grid;
  place-items: center;
  min-height: 100vh;
}

.em-ps {
  padding: 10px;
  font-size: 15px;
}

h3,
span {
  color: #ccccff;
  font-size: 20px;
}

.logout {
  font-size: 20px;
  border-radius: 10px;
}

* {
  font-family: "Jua", sans-serif;
}

